<template>
  <!-- <div class="scrollPage" ref="goodsPageScroll"> -->
  <div class="activityTemplate" :style="{'background':imgUrl==''?bgcolor:'url('+ imgUrl+')'}">
    <!-- 顶部标题 -->
    <!-- <van-nav-bar class="title" :title="sourceList.title" @click-left="goToBack" left-arrow :style="setTopHeight">
            
    </van-nav-bar>-->
    <navigation-bar
      class="title"
      @onLeftClick="goToBack()"
      :pageName="sourceList.title"
      :style="setTopHeight"
    >
      <template v-slot:nav-right class="rightIcon">
        <!-- <van-icon class="share" :name="img.share" @click="wechartShare" /> -->
        <van-icon
          class="cart"
          :name="img.shoppingCart"
          :info="goodsNum!=0?(goodsNum<=99?goodsNum+'':'99+'):''"
          @click="turnToCart"
        ></van-icon>
      </template>
    </navigation-bar>
    <!-- 商品列表 -->
    <div class="goodsVantList" style="width:100%" ref="goodsPageScroll">
      <div class="goodsList" v-show="ifNetWorkError==false">
        <div
          class="goodsItem"
          v-for="(goodsItem,goodsIndex) in goodsListData"
          :key="'goodsIndex'+goodsIndex"
          @click="goToProduct(goodsItem)"
        >
          <div class="goodsImg">
            <img :src="goodsItem.spuPicUrl" alt @error="defImg" />
            <div class="showActivitySpu" v-show="goodsItem.activitySpuId">秒杀</div>
          </div>
          <div class="goodsInfo">
            <div class="goodsName">{{goodsItem.spuName}}</div>
            <div class="labelList">
              <div class="labelItem">
                {{goodsItem.shopName}}
                <div class="border"></div>
              </div>
              <div class="labelItem" v-show="goodsItem.couponFlag==1">
                券
                <div class="border"></div>
              </div>
            </div>
            <div class="goodsBottomInfo">
              <div class="goodsPrice">
                <div class="priceAndSale">
                  <div class="goodsPriceNum">￥{{goodsItem.skuPrice}}</div>
                  <div class="saleNum">月售{{goodsItem.virtualSale}}</div>
                </div>
                <div class="lineGoodsPriceNum" v-if="goodsItem.priceLine">￥{{goodsItem.priceLine}}</div>
              </div>
              <div class="addBtn" @click.stop="addToCart(goodsItem)">
                <img src="@/assets/images/mall/mall_icon_add.png" alt />
              </div>
            </div>
          </div>
        </div>
      </div>
      <netWorkError v-show="ifNetWorkError"></netWorkError>
    </div>
    <!--点击分享弹窗-->
    <van-action-sheet
      v-model="weChartShare"
      round
      cancel-text="取消"
      @cancel="onCancel"
      safe-area-inset-bottom
    >
      <p class="shareTit">分享至</p>
      <div class="share-box" :style="{'display':'flex','justify-content':'center'}">
        <div class="shareChart" @click="checkStoragePermissions(1)">
          <img :src="img.shareChart" />
          <p>微信好友</p>
        </div>
        <div class="shareFriends">
          <img :src="img.shareFriends" @click="checkStoragePermissions(2)" />
          <p>朋友圈</p>
        </div>
      </div>
      <div class="borderLine"></div>
    </van-action-sheet>
  </div>
  <!-- </div> -->
</template>
<style scoped lang="scss">
/deep/.van-nav-bar__left {
  width: 40px;
  left: 0 !important;
}
.scrollPage {
  // overflow: auto!important;
  height: 100%;
  display: flex;
  flex-direction: column;
}
