}

.goodsList {
  padding: 0 15px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.goodsItem {
  width: 168px;
  background: #ffffff;
  box-shadow: 0 0 5px 0 rgba(194, 194, 194, 0.5);
  border-radius: 4px;
