  .goodsInfo {
    padding: 3px 5px 5px;

    .goodsName {
      height: 40px;
      overflow: hidden;
      line-height: 20px;
      font-family: PingFangSC-Medium;
      font-size: 14px;
      color: #333333;
      letter-spacing: -0.05px;
    }
    .labelList {
      display: flex;
      flex-wrap: wrap;
