          border: 1px solid #fb5768;
        }
      }
    }
    .goodsBottomInfo {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 5px;

      .goodsPrice {
        .priceAndSale {
          display: flex;
          align-items: center;

          .goodsPriceNum {
            font-family: PingFangSC-Medium;
            font-size: 14px;
            color: #f43d3d;
            letter-spacing: -0.04px;
            line-height: 14px;
            margin-right: 6px;
          }

          .saleNum {
            font-family: PingFangSC-Regular;
            font-size: 10px;
            color: #9a9a9a;
            letter-spacing: -0.03px;
            line-height: 10px;
          }
        }

        .lineGoodsPriceNum {
          font-family: PingFangSC-Regular;
          font-size: 10px;
          color: #9a9a9a;
          letter-spacing: -0.03px;
          line-height: 10px;
          text-decoration: line-through;
          margin-top: 3px;
        }
      }

      .addBtn {
        width: 22px;
        height: 22px;
        display: flex;
        img {
          width: 100%;
        }
      }
    }
  }
}
/*低库存*/
.lowStock {
  width: 96%;
  height: 25px;
  opacity: 0.5;
  background: #000000;
  position: absolute;
  display: block;
  top: 160px;
