  font-size: 12px;
  color: #ffffff;
  letter-spacing: -0.04px;
  text-align: center;
  line-height: 25px;
}
.noStore {
  width: 80px;
  height: 80px;
  position: absolute;
  top: 40px;
  left: 53px;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.5);
  font-size: 15px;
  color: #ffffff;
  letter-spacing: -0.04px;
  line-height: 70px;
  text-align: center;
  line-height: 80px;
}
//微信分享
.shareTit {
  font-family: PingFangSC-Medium;
  font-size: 16px;
  color: #333333;
  letter-spacing: -0.05px;
  text-align: center;
  height: 22px;
  line-height: 22px;
  padding: 25px 0;
  margin: 0;
}
.share-box {
  height: 120px;
  margin: 0 15px;
  box-sizing: border-box;
  border-bottom: 0.5px solid #c8c8c8;
  img {
    width: 64px;
    height: 64px;
  }
  p {
    height: 21px;
    line-height: 21px;
    margin: 0;
    margin-top: 4px;
  }
}
.shareChart {
  font-family: PingFangSC-Regular;
  font-size: 12px;
  color: #9a9a9a;
  letter-spacing: -0.04px;
  text-align: center;
  line-height: 21px;
  margin-right: 15px;
}
.shareFriends {
  /* width: 64px; */
  font-family: PingFangSC-Regular;
  font-size: 12px;
  color: #9a9a9a;
  letter-spacing: -0.04px;
  text-align: center;
  line-height: 21px;
  margin-left: 15px;
}
.shareChart img {
  width: 64px;
}
.shareFriends img {
  width: 64px;
}
/deep/.van-action-sheet__cancel::before {
  height: 0px;
}
/deep/.van-action-sheet__cancel {
  height: 65px;
  line-height: 65px;
  font-family: PingFangSC-Medium;
  font-size: 16px;
  color: #3a3a3a;
  letter-spacing: 0.06px;
}
/deep/.van-icon__image {
  width: 28px;
}
/deep/.van-info {
  top: 5px;
  right: 10px;
  height: 15px;
  font-size: 8px;
  min-width: 15px;
  line-height: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
<script>
import {
  getActivityTepmlateGoods,
  getActivityTepmlate,
  getCartNum,
  addToCart,
  getActivityUrl
} from "../../services/productInfoService";
import vueWaterfallEasy from "vue-waterfall-easy";
import NavigationBar from "@/common/currency/NavigationBar.vue";
import { mapActions, mapState } from "vuex";
import { Toast ,Dialog} from "vant";
import Vue from "vue";
import netWorkError from "../../common/networkError/NetworkError.vue";

export default {
  data() {
    return {
      setVantHeight: {}, //设置为你推荐的高度
      goodsListData: [], //商品集合
      sourceList: {
        imgUrl: ""
      }, //模板信息
      imgUrl: "",
      bgcolor: "",
      goodsNum: "", //购物车角标显示商品数量
      goodsLoadFinished: false,
      finished: false,
      loading: false,
      img: {
        shoppingCart: require("@/assets/images/mall/mall_icon_shoppingcart_black.png"),
        share: require("@/assets/images/mall/mall_icon_share.png"),
        addToCart: require("@/assets/images/mall/mall_icon_add.png"),
        shareChart: require("@/assets/images/common/nav_icon_share_wechat.png"),
        shareFriends: require("@/assets/images/common/nav_icon_share_moments.png"),
        XWJLogo: require("@/assets/images/common/login_logo.png"),
        defaultXWJPic: require("@/assets/images/default/default_picture.png")
      },
      weChartShare: false, //微信分享窗口
      shareUrl: "", //微信分享链接
      templateId: 0,
      scroll: 0,
      ifNetWorkError: false, //网络异常标志
      ifGoodsRefresh: false,
      ifTemplateRefresh: false,
      isRefresh: false, //是否下拉刷新
      setTopHeight: {
        "padding-top": this.$stbHeight + "px"
      }
    };
  },
  components: {
    vueWaterfallEasy,
    netWorkError,
    NavigationBar
  },
  methods: {
    goToBack() {
      this.$router.go(-1);
    },
    // 商品图片加载失败时显示默认图片
    defImg() {
      let img = event.srcElement;
      img.src = this.img.defaultXWJPic;
      img.onerror = null;
    },
    // 跳转购物车
    turnToCart() {
      this.$router.push({ path: "/shoppingCart" });
    },
    //获取商品活动页模板和商品信息
    getActivityLists() {
      console.log("获取商品信息" + this.templateId);
      const data = {
        id: this.templateId,
        areaCode: this.$userInfo.AREA_CODE
      };
      getActivityTepmlateGoods(data)
        .then(Response => {
          // this.getActivityTepmlate();
          this.imgUrl = this.sourceList.imgUrl; //同时显示背景色和商品
          this.bgcolor = this.sourceList.bgcolor;
          this.goodsListData = Response.data;
          this.goodsListData.map(item => {
            Vue.prototype.$isHasLocalImage(item.spuPicUrl, function(
              localImgUrl
            ) {
              item.spuPicUrl = localImgUrl;
            });
          });
          this.ifNetworkError = false;
        })
        .catch(error => {
          if (this.ifGoodsRefresh == false) {
            this.ifGoodsRefresh = true;
            this.getActivityLists();
          } else {
            console.log("网络异常====>" + this.ifNetworkError);
            this.$toast("网络异常，请稍后重试");
            this.ifNetworkError = true;
          }
        });
    },
    //获取活动模板信息
    getActivityTepmlate() {
      const data = {
        id: this.templateId
      };
      getActivityTepmlate(data)
        .then(Response => {
          this.sourceList = Response.data;
          this.getActivityLists();
          this.ifNetworkError = false;
        })
        .catch(error => {
          if (this.ifTemplateRefresh == false) {
            this.ifTemplateRefresh = true;
            this.getActivityTepmlate();
          } else {
            this.$toast("网络异常，请稍后重试");
            this.ifNetworkError = true;
          }
        });
    },
    //添加商品到购物车
    addToCart(goodsItem) {
      console.log("购物测" + JSON.stringify(goodsItem));
      if (this.goodsListData.storeNumber != 0) {
        const chartData = {
          ownerCode: this.$userInfo.OWNER_CODE,
          spuCode: goodsItem.spuCode,
          skuNo: goodsItem.skuNo,
          shopCode: goodsItem.shopCode,
          activitySpuId:goodsItem.activitySpuId,
          skuCount: 1,
          mark: 1
        };
        addToCart(chartData).then(response => {
          this.$toast("添加购物车成功");
          this.getCartNum();
        });
      } else {
        this.$toast("该商品正在努力补货中哦");
      }
    },
    //获取购物车角标数量
    getCartNum() {
      const _this = this;
      const data = {
        ownerCode: this.$userInfo.OWNER_CODE
      };
      getCartNum(data).then(function(response) {
        _this.goodsNum = response.data.skuTypeNum;
      });
    },
    // 点击分享弹窗
    wechartShare: function() {
      this.weChartShare = true;
    },
    shareAction(x) {
      if (this.hasStoragePermission) {
        this.shareToFriend(x);
      } else {
        Dialog.confirm({
          message: "请您打开存储权限，以正常使用分享功能"
        })
          .then(() => {
            this.requestStoragePermissions(x);
          })
          .catch(() => {
            //oncancel
          });
      }
    },
    //分享到朋友圈或者微信好友
    shareToFriend(x) {
      console.log("模板id" + this.templateId);
      const data = {
        ownerCode: this.$userInfo.OWNER_CODE,
        areaCode: this.$userInfo.AREA_CODE,
        shareTo: x, //0代表什么，1代表什么
        advertId: this.templateId
      };
      getActivityUrl(data).then(response => {
        if (response.resultCode == "0") {
          this.weChartShare = false;
          this.shareUrl = response.data;
          Wechat.share(
            {
              message: {
                title: this.sourceList.title,
                description: this.sourceList.remark.length == 0 ? "更多精彩，尽在信我家" : this.sourceList.remark ,
                thumb:
                  "https://xwjfile.juhaolian.cn/hisense/download/others/xinwojia.png", //需要从服务器上获取
                media: {
                  type: Wechat.Type.WEBPAGE,
                  webpageUrl: this.shareUrl
                }
              },
              scene: x == 1 ? Wechat.Scene.SESSION : Wechat.Scene.TIMELINE //1是微信好友 2是朋友圈
            },
            function() {
              //alert("Success");
            },
            reason => {
              this.$toast(reason); //无微信时提示用户未安装微信
            }
          );
        } else {
          this.$toast("分享失败，请稍后重试");
        }
      });
    },
    checkStoragePermissions(x) {
      const platform = navigator.userAgent;
      const isiOS = !!platform.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
      let _this = this;
      if (isiOS) {
        this.hasStoragePermission = true;
        this.shareAction(x);
      } else {
        var list = "android.permission.WRITE_EXTERNAL_STORAGE";
        var permissions = cordova.plugins.permissions;
        permissions.checkPermission(
          list,
          function(status) {
            if (status.hasPermission) {
              _this.hasStoragePermission = true;
              _this.shareAction(x);
            } else {
              _this.hasStoragePermission = false;
              _this.shareAction(x);
            }
          },
          function() {
            _this.hasStoragePermission = false;
            _this.shareAction(x);
          }
        );
      }
    },
    requestStoragePermissions(x) {
      const platform = navigator.userAgent;
      const isiOS = !!platform.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
      let _this = this;
      if (isiOS) {
        this.hasStoragePermission = true;
        this.shareAction(x);
      } else {
        var list = ["android.permission.WRITE_EXTERNAL_STORAGE"];
        var permissions = cordova.plugins.permissions;
        permissions.requestPermissions(
          list,
          function(status) {
            if (status.hasPermission) {
              _this.hasStoragePermission = true;
              _this.shareAction(x);
            } else {
              _this.hasStoragePermission = false;
            }
          },
          function() {
            _this.hasStoragePermission = false;
          }
        );
      }
    },
    //取消微信分享
    onCancel: function() {
      this.weChartShare = false;
    },
    //去商品详情页
    goToProduct(goodsItem) {
      if(goodsItem.activitySpuId){
                    this.$router.push({
                        name: 'flashSaleGoods',
                        query: {
                            spuCode:goodsItem.spuCode,
                            activitySpuId:goodsItem.activitySpuId,
                            goodsUrl:goodsItem.spuPicUrl
                        }
                    })
                }else{
                    this.$router.push({
                        name: 'goodsDetail',
                        params: {
                            spuCode:goodsItem.spuCode,
                            goodsUrl:goodsItem.spuPicUrl
                        }
                    })
                }
    }
  },
  mounted: function() {},
  activated() {
    if (this.$route.params.templateId) {
      this.templateId = this.$route.params.templateId;
      this.getActivityTepmlate();
      this.getCartNum();
      this.$refs.goodsPageScroll.scrollTop = this.scroll = 0;
    } else {
      this.$refs.goodsPageScroll.scrollTop = this.scroll;
      this.getCartNum();
      this.getActivityLists();
    }
  },
  computed: mapState({
    setActivityCode: state => state.productPage.setActivityCode,
  }),
  watch: {
      
    setActivityCode:{
          handler(newval, oldval) {
              if(newval != this.templateId&&newval!=-1){
                  this.templateId=newval;
                  this.getActivityTepmlate();
                  this.$store.commit("setActivityCodeChange", {
                    activityCode: -1
                  });
              }
          },
      },
      
  },
  beforeRouteLeave(to, from, next) {
    this.scroll = this.$refs.goodsPageScroll.scrollTop;
    if (to.name != "goodsDetail" && to.name != "shoppingCart") {
      (this.goodsListData = []), //商品集合
        (this.sourceList = {}), //模板信息
        (this.imgUrl = ""),
        (this.bgcolor = "");
    }
    next();
  }
};
</script>
